.Team {
    background-color: #292b30;
    width: 100%;
    min-height: 600px;
    box-shadow: 0 0 1px 0 rgba(0, 0, 0, .5);
    padding: 3rem 10% !important;
}

.Team__Header {
    color: #ffffff;
    letter-spacing: 1.5rem;
}

.Team__Desc {
    color: #ffffff;
    text-align: center;
}